  top: 20px;
  left: 20px;
}

.form-container {
  color: #fff;
  z-index: 999;
  background-color: #bea66e;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: bottom .5s;
  display: flex;
  position: fixed;
  bottom: -100%;
  left: 0;
  right: 0;
}

.form-container.open {
  z-index: 999;
  transition: bottom .5s;
  top: 50%;
